angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/home/home.html","<div class=\"fill\">\n	<div class=\"container content mt-3\" scroll-glue>\n		<div class=\"row mb-2\" ng-repeat=\"message in vm.messages\">\n			<div class=\"col-1\">\n				<img class=\"rounded\" ng-src=\"content/images/{{message.user}}.png\">\n			</div>\n			<div class=\"col-11\">\n				<div class=\"card\" ng-class=\"message.user == \'me\' ? \'card-outline-warning\' : \'card-outline-info\'\">\n					<div class=\"card-block\">\n						<span class=\"card-title\"><strong class=\"capitalize\">{{message.user}}</strong></span>\n						<span class=\"card-subtitle\"><small class=\"ml-1 text-muted\">{{message.timestamp | amDateFormat: \'HH:mm\'}}</small></span>\n						<p class=\"card-text\" translate=\"{{message.content}}\" translate-values=\"{{message.values}}\"></p>\n					</div>\n				</div>\n			</div>\n		</div>\n	</div>\n	<div class=\"container\">\n		<div class=\"row\">\n			<div class=\"col-12\">\n				<form ng-submit=\"vm.submit()\">\n					<div class=\"input-group\">\n						<span class=\"input-group-btn\">\n					<button type=\"button\" ng-click=\"vm.toggleRecording()\" class=\"btn btn-lg\" ng-class=\"vm.isRecording() ? \'btn-danger\' : \'btn-success\'\">\n						<i class=\"fa fa-2x\" ng-class=\"vm.isRecording() ? \'fa-microphone-slash\' : \'fa-microphone\'\" aria-hidden=\"true\"></i>\n			</button>\n				</span>\n						<input type=\"text\" class=\"form-control\" ng-model=\"vm.text\" placeholder=\"{{ \'home.form.placeholder\' | translate }}\">\n					</div>\n				</form>\n			</div>\n		</div>\n	</div>\n</div>\n");
$templateCache.put("app/settings/settings.html","<div class=\"container mt-3\">\n  <form>\n    <h4 translate=\"settings.stt.title\">Speech Recognition</h4>\n    <div class=\"form-group row\">\n      <label for=\"locale\" class=\"col-sm-2 col-form-label\" translate=\"settings.stt.locale\">Locale</label>\n      <div class=\"col-sm-10\">\n        <input type=\"text\" class=\"form-control\" id=\"locale\" placeholder=\"en-US,fr-FR,..\" ng-model=\"vm.settings.stt.locale\" uib-typeahead=\"locale for locale in vm.locales | filter:$viewValue | limitTo:3\">\n      </div>\n    </div>\n    <div class=\"form-group row\">\n      <div class=\"offset-sm-2 col-sm-10 text-right\">\n        <button type=\"submit\" class=\"btn btn-primary\" ng-click=\"vm.save()\"><i class=\"fa fa-floppy-o\" aria-hidden=\"true\"></i>&nbsp;<span translate=\"settings.save\">Save</span></button>\n      </div>\n    </div>\n  </form>\n</div>\n");}]);